import React, { Component } from "react";
import Slider from "react-slick";
import FittedImage from 'react-fitted-image';
import './Slider.css';
import {isMobile} from 'react-device-detect';

export default class SimpleSlider extends Component {
  render() {
    const settings = {
      dots: this.props.dots,
      infinite: true,
      autoplay: true,
      arrows:true,
      speed: 500,
      swipeToSlide: true,
      slidesToShow: this.props.slidestoshow,
      slidesToScroll: 1
    };
    if(isMobile){
      settings.arrows = false
    }
   
    return (
      <div >
        <Slider {...settings} className="slider" >
          <div >
           <FittedImage fit="cover" src="https://www.dhresource.com/0x0/f2/albu/g8/M00/15/91/rBVaV13nKj2AUzRsAAGAEr-alGc122.jpg"className="sliderimg" alt=""></FittedImage>
          </div>
          <div>
           <FittedImage fit="cover" src="https://www.dhresource.com/0x0/f2/albu/g10/M00/7C/79/rBVaVl3nKl-AUFmlAAGswwMeXVI994.jpg"className="sliderimg" alt=""></FittedImage>
          </div>
          <div>
          <FittedImage fit="cover" src="https://www.dhresource.com/0x0/f2/albu/g8/M00/A9/B7/rBVaVF3vA5mAJqVqAAFlA7w3NEk078.jpg"className="sliderimg" alt=""></FittedImage>

          </div>
         
        </Slider>
      </div>
    );
  }
}